* {
   font-family: Helvetica, Arial, sans-serif;
}

body {
   padding: 60px;
}

input {
   color: grey;
   font-size: 1.2rem;
   width: 100%;
}

.card-grid {
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
}

.card {
   align-content: center;
   border: 1px solid grey;
   border-radius: 6px;
   margin-bottom: 10px;
   margin-right: 10px;
   overflow: hidden;
   max-width: 350px;
}

.card p {
   padding: 5px;
   text-align: center;
}

.card img {
   max-width: 100%;
   height: auto;
}
